function getDate(type) {
	const date = new Date();

	let year = date.getFullYear();
	let month = date.getMonth() + 1;
	let day = date.getDate();

	if (type === 'start') {
		year = year - 60;
	} else if (type === 'end') {
		year = year + 2;
	}
	month = month > 9 ? month : '0' + month;;
	day = day > 9 ? day : '0' + day;

	return `${year}-${month}-${day}`;
}
export default {
	data(){
		return {
			newUserInfoItem:{},
			userInfoObj:{},
			// 姓名
			iptVal:"",
			array: [{name:'男'},{name: '女'}],
			index: 0,
			tiem:"",
			sex:"",
			date: getDate({
				format: true
			}),
			startDate:getDate('start'),
			endDate:getDate('end'),
		}
	},
	onShow(){
		this.$request.GET({
			url:this.$api.apiUrl.GET_USER_INFO,
		}).then(res=>{
			let result = res.data;
			this.newUserInfoItem = res.data;
			this.iptVal = result.nickname
			this.tiem = result.birthday;
			this.index = this.array.findIndex(item=>{
				return result.sex === item.name
			})
		})
	},
	methods:{
		bindPickerChange: function(e) {
			this.index = e.target.value
			// 性别
			this.sex = this.array[this.index].name;
		},
		bindDateChange: function(e) {
			// 出生年月日
			this.tiem = e.target.value
		},
		saveSeting(){
			this.$request.POST({
				url:this.$api.apiUrl.POST_USER_EDIT_INFO,
				data:{
					nickname:this.iptVal,
					sex:this.index,
					birthday:this.tiem
				}
			}).then(res=>{
				// console.log(res);
				if(res.errcode === 1){
					uni.showToast({
						title:res.errmsg,
						success:()=>{
							uni.navigateTo({
								url:"/pages/mys/info/info"
							})
						}
					})
				}
			})
		}
	}
}
